import wx from 'weixin-js-sdk'

let url = location.href.split('#')[0];
let img='http://txjs-wechat-hnw.mion.cn/logo.jpg';
window.link='';
window.title='';
const desc='';
export const share = {
  methods: {
    share() {
      this.$http.get('/third/jssdk/get-sign-package', {
        params: {
          url: url,
          APIs: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone']
        }
      }).then(r => {
        wx.config(r.data.info)
      });
      wx.error(function (res) {
        alert("" + res.errMsg);
      });
      wx.ready(function () {
        wx.checkJsApi({
          jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone'],
          success: function (res) {

          }
        });
        wx.onMenuShareTimeline({
          title: window.title, // 
          link: window.link, // JS
          desc: desc, // 
          imgUrl: img, // 
          success: function () {
            // 
          },
        });
        wx.onMenuShareAppMessage({
          title: window.title, // 
          desc: desc, // 
          link: window.link, // JS
          imgUrl: img, // 
          type: '', // ,musicvideolinklink
          dataUrl: '', // typemusicvideo
          success: function () {
            // 
          }
        });
        wx.onMenuShareQQ({
          title: window.title, // 
          desc: desc, // 
          link: window.link, // 
          imgUrl: img, // 
          success: function () {
// 
          },
          cancel: function () {
// 
          }
        });
        wx.onMenuShareWeibo({
          title: window.title, // 
          desc: desc, // 
          link: window.link, // 
          imgUrl: img, // 
          success: function () {
// 
          },
          cancel: function () {
// 
          }
        });
        wx.onMenuShareQZone({
          title: window.title, // 
          desc: desc, // 
          link: window.link, // 
          imgUrl: img, // 
          success: function () {
// 
          },
          cancel: function () {
// 
          }
        });
      });
    }
  }
}



